export const reconvertDay = (day) => {
  let result = "";
  switch (day) {
    case "MONDAY":
      result = "";
      break;
    case "TUESDAY":
      result = "";
      break;
    case "WEDNESDAY":
      result = "";
      break;
    case "THURSDAY":
      result = "";
      break;
    case "FRIDAY":
      result = "";
      break;
    case "SATURDAY":
      result = "";
      break;
    case "SUNDAY":
      result = "";
      break;
    default:
      result = "";
  }
  return result;
};

